@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.main {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
}
.label {
    z-index: 100;
    min-width: 140px;
    box-shadow: -10px 0px 30px 10px rgba(0,0,0,0.30);
}

.label-section {
    padding: 0px 5px;
    color: #888888;
    font-weight: 500;
    font-size: 1.2rem;
    text-align: center;
    border-bottom: 2px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
}

.label-section:last-child {
    border-bottom: none;
}
.weather {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: hidden;
}

.touchpad {
    position: fixed;
    top: 0;
    left: 140px;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0);
    z-index: 100;
}

.left-arrow {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(255, 255, 255, 1);
    font-size: 2.5rem;
    top: 50%;
    width: 100px;
    height: 50px;
    background-color: rgba(0, 0, 0, .3);
    border-top-left-radius: 110px;
    border-top-right-radius: 110px;
    transform: rotate(90deg) translate(-50%, 50%);
}

.right-arrow {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
    color: rgba(255, 255, 255, 1);
    top: 50%;
    right: 0;
    width: 100px;
    height: 50px;
    background-color: rgba(0, 0, 0, .3);
    border-top-left-radius: 110px;
    border-top-right-radius: 110px;
    transform: rotate(-90deg) translate(50%, -110px);
}

.left-arrow:hover, .right-arrow:hover {
    background: rgba(0, 0, 0, .5);
    cursor: pointer;
}

#canv {
    position: absolute;
}

.scroll-bar {
    position: absolute;
    bottom: 0;
    height: 8px;
    opacity: 0;
    width: 100%;
    background: rgba(0, 0, 0, .3);
    transition: opacity .75s;
}

.bar {
    position: absolute;
    height: 100%;
    background: rgba(0, 0, 0, .5);
}
.forecast-bar {
    min-width: 140px;
}
.forecast-bar_day {
    height: 3.3%;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    color: gray;
    text-transform: uppercase;
    font-weight: 700;
}
.forecast-bar_hour {
    height: 6.1%;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 1.7rem;
}
.forecast-bar_forecast {
    height: 7.2%;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
}
.forecast-bar_temperature {
    height: 24%;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
}

.temperature {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
    font-weight: 500;
}

.t {
    z-index: 99;
    color: #f9d348;
}
.forecast-bar_rain-drop {
    height: 12.9%;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #eeeeee;
    position: relative;
}

.rain {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #34ccff;
}

.rain-height {
    position: absolute;
    left: 50%;
    font-size: 1rem;
    font-weight: 700;
    transform: translateX(-50%);
}
.forecast-bar_wind-dir {
    height: 12.9%;
    background-color: #eeeeee;
    border-bottom: 2px solid #eeeeee;
    border-left: 2px solid #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}

.direction {
    font-size: 0.9rem;
    margin-top: 10px;
}
.forecast-bar_wind-speed {
    height: 10.6%;
    background-color: #eeeeee;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #ffffff;
    font-weight: 400;
    font-size: 1.3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.wind-strength {
    font-size: 1rem;
}
.forecast-bar_pressure {
    height: 21.6%;
    border-left: 2px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pressure {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 500;
}
* {
    font-family: 'Roboto', sans-serif;
}

body {
    margin: 0;
    padding: 0;
    height: 100vh;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}
